@font-face {
    font-family: 'AverageSans-Regular';
    src: local('AverageSans-Regular'),url('../../fonts/AverageSans-Regular.ttf') format('truetype'),;
}

body ,html{
  margin: 0;
  font-family: 'AverageSans-Regular';
  font-size  : 16px;
}

/* Custom start */
.lowshadow{
    box-shadow:0px 0px 5px #cdcdcd;
}

.color_secondary{
    color: #ff9800 !important;
}


/* Custom end */

/* Override start */
.ant-layout-footer{
    position: fixed;
    width: 100%;
    bottom: 0;
}

.ant-form-vertical .ant-form-item-label >label::before{
    display: none !important;
}
.ant-form-vertical .ant-form-item-label >label::after{
    color: #000000;
    content: "*" !important;
    font-size: 16px;
    font-weight: 700;
    line-height: 0px;
    margin-left: 3px;
    display: inline-block;
}
.ant-input,
.ant-input-affix-wrapper{
    border: none;
    border-bottom : 1px solid #d9d9d9;
    border-radius: 0px;
    padding: 0px;
    color: #4a4343;
    box-shadow: none;
}
.ant-input:focus,
.ant-input-affix-wrapper:focus{
    box-shadow: none;
}
.ant-form-vertical .ant-form-item-label{
    padding: 0;
}
.ant-input-prefix{
    margin-inline-end: 10px !important;
}
/* override end */
